/* cabecalho */
.cabecalho {
    width: 100%;
    background-color: #fff;
  }
  .cabecalho__container {
    padding: 0 15px;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  @media (max-width: 610px) {
    .cabecalho__container {
      -ms-flex-pack: center;
          justify-content: center;
    }
  }
  .cabecalho__logo {
    padding: 13px 0px;
  }
  .cabecalho__logo a {
    color: inherit;
    text-decoration: none;
  }
  
  .cabecalho__item {
    display: inline-block;
  }


