/* dashboard */
.dashboard {
  float: left;
  width: 30%;
}
@media (max-width: 610px) {
  .dashboard {
    width: 100%;
  }
}
@media (min-width: 611px) {
  .dashboard__centro {
    width: calc(70% - 15px);
    margin-left: 15px;
    float: right;
  }
}


